import React, { Component } from 'react';
import { Button, List, NavBar, Icon} from 'antd-mobile';
import {emitter} from '../../common';
import {InputItem,Picker,DatePicker,Switch,Stepper,validateForm,getFormValues,setFormValues} from '../../components/form';
import './index.less';

class BizComp extends Component {
  constructor(props) {
    super(props);
    this.state={
      records:[{}]
    };
  }
  componentDidMount(){
    if(window.insureForm){
      this.setState({records:window.insureForm.insurants},()=>{
        setFormValues.call(this,window.insureForm);
      });
    }
  }
  render() {
    return (
      <div className="form">
        <NavBar
          mode="light"
          icon={<Icon type="left" />}
          onLeftClick={() => {
            window.insureForm = getFormValues.call(this);
            emitter.emit('showPage',{page:'detail',justHide:true});
            
          }}
          rightContent={[<div key="myInsureOrder"></div>
          ]}
        ></NavBar>
        <div>
          <div>
            <div className="head-bar"></div>
            <List>
              <InputItem ref="name" label="" parent={this} validators={[{
                rule:(val)=>{
                  if(val.length > 5)
                    return false;
                  return true;
                },msg:'5'
              }]} required>
              </InputItem>
              <InputItem ref="idNo" parent={this} label="" clear>    
              </InputItem>
              <InputItem ref="phone" parent={this} label="" type="phone">
              </InputItem>
            </List>
          </div>
          <div>
            <div className="head-bar"></div>
            <List>
              <Switch ref="isSelf" parent={this} label=""/>
              <InputItem ref="insurantName" parent={this} label="" placeholder="">
              </InputItem>
              <InputItem ref="insurantIdNo" parent={this} label="">
              </InputItem>
              <InputItem  ref="insurantPhone" parent={this} label="" type="phone">
              </InputItem>
              <Picker ref="relation" parent={this} label="" data={[{value:'1',label:''},{value:'2',label:''}]} cols={1} required>
              </Picker>
              <Stepper ref="count" parent={this} label="" showNumber min={1}>
              </Stepper>
              <DatePicker ref="validDate" parent={this} label="" mode="date" required>
              </DatePicker>
            </List>
          </div>
          {this.state.records.map((r,i)=>{return (<div key={'rec'+i}>
            <div className="head-bar"><a onClick={()=>{
              let {records} = this.state;
              records.push({});
              this.setState({records});
            }}></a></div>
            <List>
              <InputItem ref={`insurants[${i}].name`} parent={this} label="" placeholder="">
              </InputItem>
              <DatePicker ref={`insurants[${i}].validDate`} parent={this} label="" mode="date" required>
              </DatePicker>
            </List>
          </div>)})
          }
        </div>
        <div className="foot-bar">
          <div className="amount">10.00</div>
          <Button type="primary" onClick={()=>{
            //setFormValues.call(this,{insurantIdNo:'143455',name:'hehe',relation:'2',validDate:new Date()});
            let formValues = validateForm.call(this);
            if(formValues){
              console.log(formValues);
            }
          }}></Button>
        </div>

      </div>
    );
  }
}
export default BizComp;



